require('./Grid.less');
import { Singleapp } from '../singleapp'
import { Complexapp} from '../complexapp'
const Apps = (microApps, meta) => {
    if (microApps) {
        return (
            <div className="cell"> {
                meta.map(item => {
                    <Singleapp item={item} />
                })
            }
            </div>
        )
    }
}

const Groups = (homeGroups, meta) => {
    if (homeGroups) {
        return (
            <div className="cell"> {
                meta.map(item => {
                    <Complexapp item={item} />
                })
            }
            </div>
        )
    }
}

function Grid ({ meta, corpId, microApps, homeGroups }){ 

    return (
        <div className="grid">
            <Apps />
            <Groups />
        </div>
    );
};

export default Grid ;


