import PageConst from './PageConst';

export default {
    defaults(props) {
        //初始的state
        return {   empty: false, loading: true,  ...PageConst, 
        }
    },
    getUsers( ctx, data ) {
        (async () => {
            const data = {
                name: '财务部'
            }
            const msg = await ctx.http.post('/api/users/create', data)
            console.log(msg)
        })();

    },

};



