
const user ={
    user: {
        namespace: 'users/',
        methods: {
            get: ( ) => ({ path: 'message' }),
            getPersonalInfo: ({ id } ) => ({ path: ['personal'], query: {userid: id} }),
            getMicroApps: () => ({ path: ['apps']}),
            add: (data) => ({path:['create']},{options:{
                method:'POST',                
                body:JSON.stringify(data),
            }}),
        }
    }
}

export default user





