import { Route } from 'react-keeper'
import Page from './PageHome';
import NavPage from '../home-nav/PageHomeNav'
import EntryPage from '../entry/PageEntry'
import MainPage from '../main/PageMain'
export default {
    page: Page,
    route: () => (
        <div>
            <Route index component={Page} path= '/home' >
                <Route  component={NavPage} path= '/nav' ></Route>
                <Route  component={MainPage} path= '/main' ></Route>
                <Route  component={EntryPage} path= '/entry' ></Route>
            </Route>
            
        </div>)
};



