import PageConst from './PageConst';
import { parseMetaData, parseMicroApps } from '../../utils/util';
import metaData from '../../mock/meta'
import Ding from '../../dings'
const { corpId } = require(`config/${ENV}.json`);
export default {
    defaults(props) {
        //初始的state
        return {
            openAppManager: false,
            corpId: corpId,
            userId: '',
            meta: {
                admin: false,
                bannerMetas: {
                    homeBannerModels: [],
                    isHomeBannerModels: false
                },
                itemMetas: {
                    homeHeaderModel: {},
                    act: '',
                    isAdminOrBoos: false,
                    attendanceUrl: '',
                    myTasksUrl: '',
                    checkinsUrl: '',
                    notReadReportUrl: '',
                    isHomeHeaderModel: false
                },
                userlistMetas: {
                    homeGroups: [],
                    renderLine: 0,
                    isHomeGroups: false
                },
                allapplistMetas: {
                    microApps: [],
                    isMicroApps: false,
                    corpId: ''
                },
                h5Config: {},
                userInfo: {
                    name: ''
                }
            },
            ...PageConst
        }
    },
    async getUserId(ctx) {
        dd.ready(() => {
            Ding.getUserInfo().then(res => {
                console.log(res.data)
                const useInfo = res.data
                ctx.setState({ userId: useInfo.userid})
            })
        })
    },

    async getUserInfo(ctx) {
        const { userid } = ctx.getState()
        ctx.Api.user.getPersonalInfo(userid).then(res => {
            const data = res.data
            console.log(data)
            const appList = parseMicroApps(data.microAppList);
            ctx.setState({ [meta.allapplistMetas]: appList })
        })
    },
    async getMicroApps(ctx) {
        ctx.Api.user.getMicroApps().then(res => {
            const data = res.data
            ctx.setState({ [meta.userInfo]: data })
        })
    },
    async init(ctx) {
        await this.getMicroApps(ctx)
        const metainfos = parseMetaData(metaData)
        ctx.setState({ meta: metainfos,  openAppManager: true })
        await this.getUserId(ctx)
        this.getUserInfo(ctx)

    }

}



